.feed-container {
    display: grid;
    //grid-template-columns: 1fr 1fr 1fr;
    //grid-auto-rows: minmax(10rem,auto);
    grid-gap: 0.5rem;
    a {
    	color: black;
    }
}

.feed-block{
	display: grid;
	grid-template-columns: repeat(3, 1fr);
	grid-template-rows: 5vh;
    position: relative;
    align-items: center;
}

.feed-title{
	grid-column: 1;
	grid-row: 1;
}

.feed-subtitle{
	grid-column: 1/4;
	grid-row: 1;	
	justify-self: center;
	opacity: 0;
	font-size: 3vh;
}

.feed-date{
	grid-column: 3;
	grid-row: 1;
	justify-self: end;
}

.feed-block:hover {
	color: white;
	background-color: black;
	.feed-subtitle {
		opacity: 1;
	}
	.feed-title, .feed-date {
		opacity: 0.5;
	}
}