
.image-gallery {
  display:  grid;
  grid-template-columns:  repeat(6, 1fr);
  grid-gap:  1rem;
}

.photo-box {
  position: relative;
  transition: opacity 0.5s ease-out;
  img{
    width: 100%;
    height:  100%;
    object-fit: cover;
  }
}

.photo-title {
    display: block;
    opacity: 0;
    transition: opacity .5s ease-out;
    position: absolute;
    bottom: 0;
    padding: 5px;
    background-color: #000;
    left: 0;
    right: 0;
    margin: 0;
    color: #fff;
    font-size: 12px;
    font-family: sans-serif;
    font-weight: 300;
}

.photo-box:nth-child(1){
  grid-row: span 2;
  grid-column: span 4;
}

.photo-box:nth-child(2),
.photo-box:nth-child(3){
  grid-column: span 2;
}

.photo-box:nth-child(4),
.photo-box:nth-child(5){
  grid-column:span 3;
}

.photo-box:hover .photo-title{
  opacity: 0.8;
}

.image-gallery:hover > .photo-box {
  opacity: 0.5;
}

.image-gallery:hover > .photo-box:hover {
  opacity: 1;
}

@media screen and (max-width: 650px) {
  .image-gallery{
    display: block;
  }
  .photo-box{
    margin-bottom: 1rem;
  }
}

