/*Layout variables 40px*/

$header-height: 8%;
$header-spacing: 4%;
$header-background-color: grey;
$header-text-color-main: white;
$header-text-color-alt: black;
$header-font-size: 1rem;

nav {
    //text-align: right;
    //align-self: center;
    a, a.visited {
        margin: 0.5rem;
        text-decoration: none;
        text-transform: uppercase;
        color: $header-text-color-alt;
        background-color: $header-text-color-main;
        font-family: $navigation-font;
        font-size: $header-font-size;
        font-weight: 500;
        letter-spacing: .05em
    }

    a:hover, a.active {
        background-color: $header-text-color-alt;
        color: $header-text-color-main;
    }
}
