/* CSS for fonts, and basic styling as well as maybe special styles */
@import url('https://fonts.googleapis.com/css2?family=Didact+Gothic&family=Hind:wght@300;400;500;600;700&display=swap');

$primary-font:  -apple-system, BlinkMacSystemFont, San Francisco, "Segoe UI", Roboto, Helvetica, Arial, sans-serif;
$alt-font: 'Didact Gothic', sans-serif;
$header-font: 'Futura', sans-serif;
$navigation-font: 'Helvetica', sans-serif;


$primary-text-colour: black;
$alt-text-colour: grey;
$link-text-colour: red;

html {
  margin: 0;
  font-family: $primary-font;
}

h1 {
  
  font-family: $header-font;
}

h2 {
  font-family: $header-font;
}

h3 {
  font-family: $header-font;
}

h4 {
  font-family: $header-font;
}

p {
  font-family: $primary-font;
}

a {
  text-decoration: none;
  color: $link-text-colour;
}
