.container {
    display:  grid;
    grid-template-columns: repeat(12,1fr) ;
    grid-template-rows: 5vh 5vh auto;
}

header {
    grid-column: 8 / span 5;
    grid-row: 1;
    background-color: white;
    align-self: center;
    //justify-self: right;
}
page-title {
    grid-column: 1/5;
    grid-row: 1;
    text-align: center;
    padding: 0;
    color: black;
    font-size: default;
    text-transform: lowercase;
    a {
        font-size: 2rem;
        color: black;
    }
}

main {
    grid-column: 3/11;
    grid-row:  3;
}

.leftside {
    grid-row: 3;
    grid-column: 1;
}

.rightside {
    grid-row: 3;
    grid-column: 2/4 
}

@media screen and (max-width: 650px) {
  main{
    grid-column: 2/12;
    grid-row: 3;
  }
  header{
    grid-column: span 12;
    grid-row: 1;
  }
  page-title{
    grid-column: 6 / span 6;
    grid-row: 2;
  }
}
